// src/app/api/demo/paid-hello/route.ts
import { NextRequest, NextResponse } from "next/server";

export async function GET(request: NextRequest) {
  const tier = request.nextUrl.searchParams.get("tier") ?? "basic";

  let amount = "100000";      // 0.1 USDC (6 decimals)
  let description = "Paid Hello (basic)";

  if (tier === "pro") {
    amount = "500000";        // 0.5 USDC
    description = "Paid Hello (pro)";
  }

  const body = {
    ok: true,
    tier,
    amount,
    currency: "USDC",
    description,
  };

  return NextResponse.json(body, { status: 200 });
}
